@import url('https://fonts.googleapis.com/css2?family=Homemade+Apple&family=Satisfy&display=swap');

#cOrderHeader, .cOrderDesc{
    text-align: center;
}

.cOrderDesc{
    margin: 0 1em 1.5em 1em;
}

#cOrderHeader{
    font-family: 'Satisfy', cursive;
    font-size: 2.5em;
    margin: .5em;
}

#cOrderBErrors{
    color: white;
    background-color: #E57373;
    border: solid 4px red;
    border-radius: 2em;
    width: 60vw;
    margin: 0 auto;
    padding: 1em
}

#cOrderBErrors div{
    margin: .5em 0;
}

.cOrderFormWrapper{
    margin: 0 auto;
    max-width: 50rem;
    border-radius: .7em;
    display: flex;
    justify-content: center;
    font-family: "Arimo";
    -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.1);
    -moz-box-shadow: 0 0 20px rgba(0,0,0,0.1);
    box-shadow: 0 0 20px rgba(0,0,0,0.1);
}

.cOrderFormContainer{
    width: 95%;
    padding: 5% 5%;
}

#cOrderForm{
    width: 100%;
}

#cOrderDeliverySection >*,
#cakeInfo >*,
#otherInfo{
    margin-top: 1em;
}

#cOrderInfoSection{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 1em;
}

#cOrderError{
    color: red;
    margin: .3rem 0 0 0;
    font-size: .9em;
}

#cOrderInfo h4{
    font-weight: normal;
    margin: 0 0 .5em 0;
    text-align: center;
}

#cOrderSection h4{
    font-weight: normal;
}

.react-slidedown.my-dropdown-slidedown {
    width: 100%
}

#cakeInfo, #otherInfo{ 
    text-align: center;
    width: 100%;
}

.cOrderRadio{
    margin: .5em;
}

.cOrderRadio input{
    margin-right: .5em;
}

.cOrderTextbox, .cOrderTextbox2, #cOrderForm select, #cOrderForm textarea{
    border: none;
    background-color: hsl(0 0% 93%);
    border-radius: .25rem;
    padding: .75rem 1rem;
}

#cOrderForm textarea{
    height: 15em;
}

#cOrderForm label{
    text-transform: uppercase;
    font-size: .8rem;
    letter-spacing: 2px;
    margin-bottom: .5em;
}

.cOrderDiv{
    justify-content: space-between;
    display: flex;
    width: 100%;
}

.cOrderDiv2{
    display: flex;
    flex-direction: column;
    width: 45%;
}

.cOrderDiv3{
    display: flex;
    flex-direction: column;
    width: 30%;
}

.cOrderDivST{
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
}

.cOrderDivST select,
.cOrderDivST input{
    width: 50%;
}

.cOrderDiv2 span, .cOrderDiv3 span{
    color: red;
    margin: .3rem 0 0 0;
    font-size: .9em;
}

.cOrderTextAreaDiv,
#otherInfo{
    display: flex;
    flex-direction: column;
    width: 100%;
}

#cOrderDate{
    background-color: hsl(0 0% 93%);
}

#cOrderButton{
    display: block;
    margin: 2em auto 0 auto;
    padding: .75rem 1rem;
    color: white;
    background-color: #4a3b76;
    text-transform: uppercase;
    border: 0;
    border-radius: .25rem;
    letter-spacing: 2px;
    cursor: pointer;
}

#cOrderButton:hover {
    animation-name: btn-hov;
    animation-duration: 550ms;
    animation-fill-mode: forwards;
}

@keyframes btn-hov {
    100% {
        background-color: #f1e8ff;
        color: #4a3b76;
        transform: scale(1.05)
    }
}

.cOrderTextbox:focus,
.cOrderTextbox2:focus,  
.cOrderSelect:focus,
.cOrderTextArea:focus{
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

#cOrderbottomSpace{
    margin-bottom: 5em;
}


@media(max-width: 750px){
    .cOrderFormWrapper{
        width: 90%;
    }

    .cOrderFormContainer {
        width: 100%;
    }

    #cOrderDeliverySection >*,
    #cakeInfo >*,
    #otherInfo{
        margin-top: 0;
    }

    #cOrderForm label{
        margin-top: 1em;
    }

    #cOrderButton {
        width: 50%;
    }

    .cOrderDiv{
        flex-direction: column;
        width: 100%;
        justify-content: space-between;
    }

    .cOrderDiv2, .cOrderDiv3, .cOrderDivST, .cOrderTextAreaDiv{
        align-items: center;
        flex-direction: column;
        width: 100%;
        display: flex;
    }

    .cOrderDiv2 input, .cOrderDiv3 input, .cOrderDiv2 select, 
    .cOrderDivST select, .cOrderDivST input, .cOrderTextArea{
        width: 100%;
    }
}