body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body
{
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 
    box-sizing: border-box;
    font-family: 'Raleway', sans-serif;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  color: black;
}

#wrapper{
  position: relative;
  min-height: 100vh;
}

#page-body{
  padding-bottom: 7em;
}

a {
  display: inline;
  text-decoration: none;
  color: inherit;
}

ul {
  padding-left: 0;
  list-style: none;
}

button{
  outline: none;
}

select{
  background: url(http://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/br_down.png) no-repeat;
  background-position: 98% 50%;
  -webkit-appearance: none;
}

textarea {
  resize: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.iconSubmitLoad{
  animation:spin 2s linear infinite;
}
@keyframes spin { 100% { transform:rotate(360deg); } }

/* Overlay for sidebars */

#overlay.hide{
  display: none;
}

#overlay.show{
  position: fixed;
  top:0;
  left:0;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.6);
  z-index: 10;
}

/* Nav */
#navBar {
  background-color: #ffcad0;
  display: flex;
  height: 7em;
  width: 100%;
}

#logoDiv {
  height: 100%;
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#logoImg {
  width: 6em;
  border: 1px solid black;
  border-radius: 100%;
}

#logoPhrase {
  display: flex;
  align-items: center;
  font-size: 95%;
  width: 45%;
  padding-left: 1em;
  vertical-align: middle;
}

#navOpenIcon, #igIcon, #navCloseIcon {
  display: none;
}

#logo {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 2em;
}

#navItems {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 60%;
  height: 100%;
  margin: 0 0 0 auto;
  font-family: 'Alata', sans-serif;
}

#navItems li {
  position: relative;
  margin-right: 1.2em;
  font-size: 1.2em;
  top: 0;
  transition: top ease 0.3s;
}

#navItems li a{
  padding: .2em 0;
}

#navItems li:hover {
  top: -5px;
  animation-name: nav-item;
  animation-duration: 500ms;
  animation-fill-mode: forwards;
}

@keyframes nav-item {
  100% {
      color: white;
      transform: scale(1.05)
  }
}

#orderOpt {
  background-color: #ffcad0;
  display: none;
  position: absolute;
  z-index: 1000;
  padding: 2em 0.5em 0.5em 0.5em;
  width: 10em;
  color: black;
  margin-top: .2em;
}

#orderOpt *{
  margin: .2em 0;
}

#orderNav:hover #orderOpt {
  display: flex;
  flex-direction: column;
}

#cartIconDiv{
  width: 5%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: .2em;
}

#cartIconDiv span{
  position: absolute;
  color: white;
  font-size: 1em;
  margin-top: .3em;
  pointer-events: none;
}

#cartIcon{
  position: relative;
  font-size: 2.5em;
  cursor: pointer;
}


@media (max-width: 850px) {
  #navBar {
    height: 6em;
  }

  #logoImg {
    width: 5em;
  }

  #logoPhrase {
    display: none;
  }

  #logo {
    width: inherit;
    justify-content: center;
  }

  #logoDiv {
    width: 100vw;
    position: relative;
  }

  #navItems {
    background-color: #ffcad0;
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 0;
    right: 0;
    width: 15em;
    justify-content: flex-start;
    height: 100vh;
    padding-top:0;
    z-index: 20;
    overflow-y: scroll;
    transition: transform 0.3s cubic-bezier(0, 0.52, 0, 1);
  }

  #navItems li {
    margin: 2em 0 0 0;
    transition: none;
  }

  #navItems li:hover {
    top: 0;
  }

  #navOpenIcon {
    display: block;
    position: absolute;
    top: 35%;
    right: .7em;
    cursor: pointer;
    font-size: 1.8em;
    z-index: 15;
  }

  #navCloseIcon{
    display: block;
    font-size: 1.8em;
    cursor: pointer;
  }

  #igIcon {
    display: block;
    position: absolute;
    top: 35%;
    left: .7em;
    cursor: pointer;
    font-size: 2em;
  }

  #navItems.hide {
    transform: translate3d(15em, 0, 0);
  }

  #navItems.show {
    transform: translate3d(0, 0, 0);
    overflow: hidden;
  }

  #orderNav:hover #orderOpt {
    display: none;
  }

  #cartIconDiv{
    position: absolute;
    top: 1.5rem;
    right: 3em;
    width: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }

}

/* Footer */

footer {
  bottom: 0;
  background-color: #ffcad0;
  width: 100%;
  text-align: center;
  padding: 0.1em 0;
  height:7em;
  position: absolute;
  padding: 0;
}

#footerItems {
  display: flex;
  font-size: 0.5;
  justify-content: center;
}

#footerItems li {
  margin: 0 2em 0 2em;
}

#igIconFooter {
  position: absolute;
  top: 40%;
  right: 3%;
  font-size: 1.8em;
}

#footerEmail{
  padding: 0;
  margin: 0;
}

/* Cart */

#cartWindow{
  position: fixed;
  width: 45vw;
  right: 0;
  top: 0;
  height: 100vh;
  z-index: 25;
  transition: transform 0.3s cubic-bezier(0, 0.52, 0, 1);
  background-color: white;
}

#cartWindow.hide {
  transform: translate3d(45vw, 0, 0);
}

#cartWindow.show {
  transform: translate3d(0, 0, 0);
}

#cartClose{
  position: absolute;
  top: .5em;
  right: .5em;
  font-size: 1.5em;
  cursor: pointer;
}

#cartHeader{
  position: absolute;
  font-size: 1.2em;
  pointer-events: none;
  top: .5em;
  left: .5em;
}

#cartTop{
  background-color: white;
  height: 3rem;
  border-bottom: 1px solid #e68893;
}

#cartOrdersWrapper{
  overflow-y: auto;
  position: absolute;
  bottom: 14.5rem;
  top: 3rem;
  width: 100%;
}

#cartOrders{
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  width: 100%;
}

#cartFreeDel{
  margin-top: .5em;
}

.cartItem{
  display: flex;
  justify-content: space-between;
  width: 85%;
  margin: .5em 0;
  padding: .5em;
  border: 3px solid #e68893;
  border-radius: 1.5em;
}

.cartItemInfo{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 50%;
}

.cartItemName, .cartItemPrice{
  margin: 0 0 .3rem 10%;
}

.cartItemName{
  font-size: 1.2em;
}

.cartItemImage{
  width: 40%;
  border-radius: 1.2em;
  z-index: -1;
  height: 7.5rem;
  object-fit: cover;
}

.cartItemCtrl{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.cartItemQty{
  font-size: 1.2em;
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none;
}

.cartItemIcon{
  font-size: 1.3em;
  color: #e68893;
  cursor: pointer;
}

.cartItemDelIcon{
  font-size: 1.2em;
  color: white;
  background-color: #e68893;
  padding: .1em;
  cursor: pointer;
}

.cartItemVertLine{
  font-weight: 200;
  font-size: 1.5em;
  color: grey;
  pointer-events: none;
}

#cartEmptyDiv{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#cartEmptyDiv span{
  font-size: 1.5em;
  margin-bottom: 1.5em;
}

#cartEmptyImage{
  width: 25%;
}

#cartCheckout{
  width: 100%;
  position: absolute;
  bottom: 0;
  background-color: white;
  height: 14.5rem;
  border-top: 1px solid #e68893;
}

#cartDeliveryDiv{
  display: flex;
  justify-content: space-between;
  width: 95%;
  margin: .5em auto;
}

#cartDeliveryDiv span{
  display: block;
  text-align: center;
  margin: .7em 0;
}

#cartDeliveryOptBtn{
  display: flex;
  justify-content: space-between;
  width: 50%;
}

#cartDeliveryOptBtn button{
  width: 45%;
  padding: .4em 0;
  font-size: .9em;
  background-color: white;
  border: solid;
  border-color: #e68893;
  border-radius: 1.5em;
  cursor: pointer;
  outline: none;
}

#cartDeliveryBtn.selected, #cartPickupBtn.selected,
#cartOnlineBtn.selected, #cartInPersonBtn.selected{
  color:white;
  background-color: #e68893;
}

.cartTotal{
  width: 95%;
  display: flex;
  justify-content: space-between;
  margin: .7em auto;
}

#cartCOButton{
  display: block;
  width: 95%;
  padding: .5em 2em;
  background-color: #e68893;
  border-radius: 1.5em;
  border: none;
  font-size: 1em;
  cursor: pointer;
  color: white;
  line-height: 1.8rem;
  font-size: 1.2rem;
  margin: .5em auto 1.5em auto;
  outline: none;
}

#cartCheckout a{
  display: inline;
}

@media (max-width: 780px) {
  #cartWindow{
    width: 100vw;
  }

  #cartWindow.hide {
    transform: translate3d(100vw, 0, 0);
  }
}
