@media (min-width: 900px){
  #wrapper .orderDiv{
    margin-top: 15vh;
  }
}

.orderDiv {
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-bottom: 5rem;
}


.orderOptionDiv {
  background-color: #ffcad0;
  width: 20rem;
  height: 20rem;
  border-radius: 75px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 200%;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  font-weight: 600;
}

.orderOptionDiv:hover {
  animation-name: optDiv-hov;
  animation-duration: 550ms;
  animation-fill-mode: forwards;
}

@keyframes optDiv-hov {
  100% {
      transform: scale(1.1)
  }
}

.orderOptionDiv span{
  margin: .25em 0;
}

@media (max-width: 900px) {
  .orderDiv{
    flex-direction: column;
    align-items: center;
  }

  .orderOptionDiv {
    margin-top: 2.5em;
  }
}
