@import url('https://fonts.googleapis.com/css2?family=Alata&display=swap');

/*Management Nav*/
#mngNav{
    width: 100%;
    display: flex;
    justify-content: space-around;
    background-color: #f1e8ff;
    margin-top: 0;
    padding: 1em 0;
    font-family: 'Alata', sans-serif;
}

/*General View Window CSS*/
#mngHeader, .mngSubHeader{
    text-align: center;
}

.mngOrderWrapper{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0,0,0,0.6);
}

.mngOrderWindow{
    background-color: white;
    position: relative;
    width: 60%;
    max-height: 80vh;
    overflow-y: auto;
    padding: 1em;
    border-radius: .6em;
}

.mngOWIcon{
    position: absolute;
    top: 2%;
    right: 2%;
    color: red;
    font-size: 1.2em;
    border-radius: .3em;
    cursor: pointer;
}

.mngItemValue{
    font-weight: bold;
}

.mngDelete{
    display: block;
    margin: 1em auto 0 auto;
    padding: .5em 1em;
    background-color: red;
    color: white;
    border-radius: 1.5em;
    border: none;
    font-size: 1em;
    cursor: pointer;
}

#mngbottomSpace{
    margin-bottom: 5em;
}

#container{
    width: 75vw;
    margin: 0 auto;
    max-height: 40em;
    box-shadow: 0 0 20px rgba(0,0,0,0.1);
    overflow: auto;
    border-radius: .7em;
}

table {
    width: 100%;
    border-collapse: collapse;
}

table th:first-child{
    border-radius:.7em 0 0 0;
}

table th:last-child{
    border-radius:0 .7em 0 0;
}

th,td {
    padding: 1em;
}

th {
    background-color: #4a3b76;
    color: white;
    text-align: start;
}

tr{
    transition: all .2s ease-in-out;
    cursor: pointer; 
}

tr:hover:not(thead tr) {
    background-color: rgba(74,59,118,.3);
    width: 80%;
}

.mngImgIndicator{
    font-size: .75em;
    text-align: center;
}

#mngImgUploadForm{
    margin: 2em 0;
    padding-bottom: .5em;
    border-bottom: 1px solid black;
}

#mngImgUploadForm label{
    margin: 2em 0;
}

#mngImgUploadForm button{
    margin: 0 auto;
    padding: .5em 1em;
    background-color: #4a3b76;
    color: white;
    border-radius: 1.5em;
    border: none;
    font-size: .8em;
    cursor: pointer;
}

.mngImgError{
    color: red;
    margin: .2em 0 0 0;
    text-align: center;
}

/*General Form CSS*/

.mngFormWrapper{
    margin: 0 auto;
    width: 75vw;
    border-radius: .7em;
    display: flex;
    justify-content: center;
    font-family: "Arimo";
    -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.1);
    -moz-box-shadow: 0 0 20px rgba(0,0,0,0.1);
    box-shadow: 0 0 20px rgba(0,0,0,0.1);
}

.mngFormContainer{
    width: 90%;
    padding: 5% 5%;
}

.mngForm{
    width: 100%;
}

.mngForm >*{
    margin-top: 1em;
}

.mngButton{
    display: block;
    margin: 2em auto 0 auto;
    padding: .75rem 1rem;
    color: white;
    background-color: #4a3b76;
    text-transform: uppercase;
    border: 0;
    border-radius: .25rem;
    letter-spacing: 2px;
    cursor: pointer;
}

.mngButton:hover {
    animation-name: btn-hov;
    animation-duration: 550ms;
    animation-fill-mode: forwards;
}

@keyframes btn-hov {
    100% {
        background-color: #f1e8ff;
        color: #4a3b76;
        transform: scale(1.05)
    }
}

/*Menu Management*/
.mngForm input, .mngForm select, .mngForm textarea{
    border: none;
    background-color: hsl(0 0% 93%);
    border-radius: .25rem;
    padding: .75rem 1rem;
    margin-top: .3em;
}

.mngForm textarea{
    height: 15em;
}

.mngForm label{
    text-transform: uppercase;
    font-size: .8rem;
    letter-spacing: 2px;
}

.mngMenuFormDiv{
    justify-content: space-between;
    display: flex;
    width: 100%;
}

.mngMenuFormDiv2{
    display: flex;
    flex-direction: column;
    width: 45%;
}

.mngFormDivST{
    display: flex;
    flex-direction: column;
    width: 100%;
}

.mngTextAreaDiv{
    display: flex;
    flex-direction: column;
    width: 100%;
}


.mngMenuTextbox:focus, .mngMenuSelect:focus, .mngTextArea:focus{
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

/*About Page*/
.mngAbtDiv{
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 3em;
    white-space: normal;
}

#mngTextAreaAbt{
    height: 25em
}

/*Responsiveness*/
@media (max-width: 750px) {

    .mngFormContainer {
        width: 100%;
    }

    .mngForm >* {
        margin-top: 0;
    }

    .mngForm label{
        margin-top: 1em;
    }

    .mngButton {
        width: 50%;
        margin-top: 2em;
    }

    .mngMenuFormDiv{
        flex-direction: column;
        width: 100%;
        justify-content: space-between;
    }

    .mngMenuFormDiv2, .mngFormDivST, .mngTextAreaDiv{
        align-items: center;
        flex-direction: column;
        width: 100%;
        display: flex;
    }

    .mngMenuFormDiv2 input, .mngFormDivST input,
    .mngFormDivST select, .mngTextArea{
        width: 100%;
    }
}
/*Other Page*/
#mngOtherdiv{
    max-width: 50rem;
    margin: 1em auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: .5rem;
    border-radius: .7em;
    font-family: "Arimo";
    -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.1);
    -moz-box-shadow: 0 0 20px rgba(0,0,0,0.1);
    box-shadow: 0 0 20px rgba(0,0,0,0.1);
}

.mngOtherdiv2{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1em 0;
}

.mngOtherdiv2 label, 
.mngODatediv label{
    font-size: 1.2em;
}

.mngOtherdiv2 input,
.mngOtherdiv2 span, 
.mngOtherdiv2 button{
    margin-top: 1em;
}

.mngOtherdiv2 span{
    margin-bottom: 0;
    width: 10em;
    font-size: 1.2em;
    text-align: center;
    border: 2px solid hsl(0 0% 93%);
    border-radius: .25rem;
    padding: .75rem 1rem;
}

.mngOtherTextbox{
    margin-bottom: 0;
    width: 10em;
    font-size: 1.2em;
    text-align: center;
    border: none;
    background-color: hsl(0 0% 93%);
    border-radius: .25rem;
    padding: .75rem 1rem;
}
 
#mngOtherButton{
    display: block;
    margin: 2em auto 0 auto;
    padding: .75rem 1rem;
    color: white;
    background-color: #4a3b76;
    text-transform: uppercase;
    border: 0;
    border-radius: .25rem;
    letter-spacing: 2px;
    cursor: pointer;
}

.mngOtherTextbox:focus{
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.mngODatediv{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1em 0;
}

.mngODatediv label{
    font-size: 1.2em;
}

.mngODatediv input{
    cursor: pointer;
}

.mngODatediv button{
    margin-top: 1em;
}

.mngODatediv label{
    margin-bottom: 1em;
}

.mngOBDatesdiv{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1em 0;
}

.mngOBDatesdiv label{
    font-size: 1.2em;
}


.mngOBDatesdiv #mngOBWrapper{
    border: solid 1px black;
    height: 20em;
    width: 15em;
    text-align: center;
    border: 2px solid hsl(0 0% 93%);
    border-radius: .25rem;
    padding: .75rem 0;
    display : flex;
    flex-direction: column;
    align-items : center;
    justify-content: flex-start;
    margin-top: 1em;
    overflow-y: auto;
}

.mngBlockedDates{
    width: 80%;
    margin: .4em 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 2px solid hsl(0 0% 83%);
    border-radius: .25rem;
    padding: .3em;
}

.mngOBIcon{
    color: red;
    cursor: pointer;
    font-size: .9em;
}

#mngDate{
    background-color: hsl(0 0% 93%);
}

/*Orders CSS*/
.mngOrdersHeader{
    margin: 0 auto;
    text-align: center;
}

.mngOrdersIcon{
    margin: 0 .2rem;
    font-size: 1.1rem;
}

/*Search CSS*/
#mngSearchWrapper{
    width: 85%;
    height: 6rem;
    margin: 0 auto 1.5rem auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

#mngSearchWrapper div{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.mngSearchTextbox{
    margin-bottom: 0;
    width: 80%;
    height: 2rem;
    padding: 0.375rem 0.75rem;
    border: 1px solid black;
    border-radius: .7rem;
    font-size: 1.2em;
}

.mngSearchSelect{
    margin-bottom: 0;
    width: 80%;
    height: 2rem;
    padding-left: 0.75rem;
    border: 1px solid black;
    border-radius: .7rem;
    font-size: 1.2em;
}

.mngSearchTextbox:focus, .mngSearchSelect:focus{
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
    vertical-align: middle;
}


#mngCustomHeader, #mngCateringHeader{
    cursor: pointer;
    display: inline-block;
}