@import url('https://fonts.googleapis.com/css2?family=Homemade+Apple&family=Satisfy&display=swap');

#menuHeader{
    text-align: center;
    font-family: 'Satisfy', cursive;
    font-size: 2.5em;
}

.menuSubHeader{
    text-align: center;
}

#menuAnnouncement{
    margin: 0 auto;
    max-width: 40em;
    padding: .5em 2em;
}

#menuAnnouncement div{
    border: solid 3px #4a3b76;
    border-radius: 1em;
    margin: 0 auto;
    height: 6em;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items:center;
}

#menuOrderMin{
    font-weight: 600;
    text-align: center;
}

@media(min-width: 800px){
    #menuWrapper{
        width: 60rem;
    }

    #menuItemWindow{
        width: 65rem;
    }
}

#menuWrapper{
    max-width: 95vw;
    margin: 0 auto 2.5em auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.menuItem{
    margin: 1em;
    padding-bottom: .8em;
    width: 20em;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    border: solid;
    border-color: #f1e8ff;
    border-width: 1px;
    border-radius: 20px;
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
    overflow: hidden;
    word-wrap: break-word;
}

.menuItem img{
    width: 100%;
    height: 15rem;
    object-fit: cover;
}

.menuItem span{
    text-align: center;
    padding: 0 .5em;
    width: 100%;
    margin: .8rem 0 0 0;
    font-size: 1.3em;
    font-weight: 200;
}

#menubottomSpace{
    margin-bottom: 5em;
}

#menuItemWindow{
    position: fixed;
    width: 40vw;
    right: 0;
    top: 0;
    height: 100vh;
    z-index: 30;
    transition: transform 0.3s cubic-bezier(0, 0.52, 0, 1);
    background-color: white;
}

#menuItemWindow.hide {
    transform: translate3d(40vw, 0, 0);
}

#menuItemWindow.show {
    transform: translate3d(0, 0, 0);
    overflow: hidden;
}

#overlay.hide{
    display: none;
}

#overlay.show{
    position: fixed;
    top:0;
    left:0;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0.6);
    z-index: 9;
}

#menuClose{
    position: absolute;
    top: .5em;
    right: .5em;
    font-size: 1.5em;
    cursor: pointer;
}

.menuImg{
    width: 100%;
    margin-top: 3em;
    height: 15rem;
    object-fit: cover;
}

#menuItemContent{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 1rem;
    font-size: 1.5em;
}

#menuItemDesc{
    margin: 1rem;
}

#menuItemSubmit{
    position: absolute;
    left: 0; 
    right: 0; 
    margin-left: auto; 
    margin-right: auto;
    text-align: center;
    bottom: 1em;
}

#menuItemQty{
    font-size: 2.5em;
    margin-bottom: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #e68893;
}

#menuItemQty span{
    margin: 0 1rem;
    -webkit-user-select: none;  
    -moz-user-select: none;    
    -ms-user-select: none;      
    user-select: none;
}

.menuQtyButton{
    cursor: pointer;
}

#menuATCButton{
    width: 95%;
    padding: .2em 0;
    background-color: #e68893;
    border-radius: 1.5em;
    border: none;
    cursor: pointer;
    color: white;
    line-height: 1.8rem;
    font-size: 1.2rem;
}

#menuATCButton span{
    -webkit-user-select: none;  
    -moz-user-select: none;    
    -ms-user-select: none;      
    user-select: none;
}

@media (max-width: 600px) {
    #menuItemWindow{
        width: 100vw;    
    } 

    #menuItemWindow.hide {
        transform: translate3d(100vw, 0, 0);
    }
}