@import url('https://fonts.googleapis.com/css2?family=Homemade+Apple&family=Satisfy&display=swap');

#aboutHeader{
    text-align: center;
    margin: 1em 0 .5em 0;
    font-family: 'Satisfy', cursive;
    font-size: 2.5em;
}

#aboutWrapper{
    padding: 0 3em;
}

#aboutBorder{
    border: solid;
    border-color: #f1e8ff;
    border-radius: 2em;
    border-width: 1px;
    padding: 2em 3em;
    max-width: 50em;
    margin: 0 auto 4em auto;
    -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.1);
    -moz-box-shadow: 0 0 20px rgba(0,0,0,0.1);
    box-shadow: 0 0 20px rgba(0,0,0,0.1);
}
