@import url('https://fonts.googleapis.com/css?family=Arimo');
@import url('https://fonts.googleapis.com/css2?family=Homemade+Apple&family=Satisfy&display=swap');

#conHeader{
    text-align: center;
}

#conHeader{
    font-family: 'Satisfy', cursive;
    font-size: 2.5em;
    margin: .5em
}

#conWrapper{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap-reverse;
}

.conInfoWrapper {
    margin: .5em;
    width: 40em;
    display: flex;
    justify-content: center;
    font-family: "Arimo";
    background-color: #FBCAD0;
    -webkit-box-shadow: 9px 13px 25px 0px rgba(0, 0, 0, 0.18);
    -moz-box-shadow: 9px 13px 25px 0px rgba(0, 0, 0, 0.18);
    box-shadow: 9px 13px 25px 0px rgba(0, 0, 0, 0.18);
    animation: slideUp 2000ms ease;
    border-radius: 25px;
}

#conCOInfoWrapper {
    animation: slideLeft 2000ms ease;
    border-radius: 25px;
}


@keyframes slideUp {
    0% {
        -webkit-transform: translateY(100%);
        transform: translateY(100%);
        visibility: visible;
    }

    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);

    }
}

@keyframes slideLeft {
    0% {
        -webkit-transform: translateX(100%);
        transform: translateX(100%);
        visibility: visible;
    }

    100% {
        -webkit-transform: translateX(0);
        transform: translateX(0);

    }
}


#conInfoContainer {
    width: 35em;
    padding: 5% 5%;
}

#conForm {
    width: 100%;
}

.conTextboxDiv >*{
    margin-top: 1.2rem;
}


#conForm label {
    text-transform: uppercase;
    font-size: 12px;
    letter-spacing: 2px;
    color: #8e2807;
}

.conHeader2 {
    font-size: 24px;
    line-height: 10px;
    color: #493b76;
    letter-spacing: 1px;
}

#conSummaryHeader{
    margin: 2rem 0 1.5rem 0;
}

.conTextboxDiv {
    justify-content: space-between;
    display: flex;
    width: 100%;
}

.conTextboxDiv div{
    display: flex;
    flex-direction: column;
    width: 30%;
}

#conAddress{
    width: 60%;
}

#conButton {
    display: block;
    margin: 2em auto 0 auto;
    padding: .8em 1.3em;
    color: white;
    background-color: #4a3b76;
    text-transform: uppercase;
    border: 0;
    border-radius: 1.1em;
    letter-spacing: 2px;
    cursor: pointer;
}

#conForm a{
    display: inline;
}

#conButton:hover {
    animation-name: btn-hov;
    animation-duration: 550ms;
    animation-fill-mode: forwards;
}

@keyframes btn-hov {
    100% {
        background-color: #f1e8ff;
        color: #4a3b76;
        transform: scale(1.05)
    }
}

button:focus {
    outline: none;
}

#conCartOrders{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

#conCartOrders::-webkit-scrollbar {
    width: 10px;               /* width of the entire scrollbar */
  }
  
  #conCartOrders::-webkit-scrollbar-track {
    background: #FBCAD0;        /* color of the tracking area */
  }
  
  #conCartOrders::-webkit-scrollbar-thumb {
    background-color: #C7C7C7;    /* color of the scroll thumb */
    border-radius: 20px;       /* roundness of the scroll thumb */
  }

.conCartItem{
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: 0 0 1.2em 0;
    padding: .5em;
    border: 3px solid #4a3b76;
    border-radius: 1.5em;
    background-color: #f1e8ff;
}

.conCartItemInfo{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 50%;
  }
  
.conCartItemName{
    margin: 0 0 .3rem 5%;
    font-size: 1.1em;
  }
  
  
.conCartItemImage{
    width: 30%;
    border-radius: 1.2em;
    height: 7.5rem;
    object-fit: cover;
}

.conCartItemCtrl{
    padding: 0 5%;
    display: flex;
    align-items: center;
}

.conCartItemCtrl span{
    margin: 0 1.5rem 0 0;
}
  
.conCartItemQty, .conCartItemPrice{
    font-size: 1.2em;
    -webkit-touch-callout: none; /* iOS Safari */
      -webkit-user-select: none; /* Safari */
       -khtml-user-select: none; /* Konqueror HTML */
         -moz-user-select: none; /* Old versions of Firefox */
          -ms-user-select: none; /* Internet Explorer/Edge */
              user-select: none;
}


#conTotalDiv{
    width: 100%;
    height: 6rem;
    margin: 1rem 0 0 0;
}

.conCost{
    width: 100%;
    margin: .5rem 0;
    display: flex;
    justify-content: space-between;
}

#conTotal{
    font-size: 1.5em;
}

#conBottomSpace{
    margin-bottom: 4em;
}

@media (max-width: 1006px) {
    #coCartInfo {
        width: 40em;
    }
}

@media (max-width: 750px) {
    #conDeliveryInfo {
        width: 95%;
    }

    #conInfoContainer {
        width: 100%;
    }

    #conButton {
        width: 50%;
    }

    .conHeader2 {
        text-align: center;
    }

    .conTextboxDiv{
        flex-direction: column;
        width: 100%;
        justify-content: space-between;
    }

    .conTextboxDiv div,
    #conAddress{
        align-items: center;
        flex-direction: column;
        width: 100%;
        display: flex;
    }

}