@import url('https://fonts.googleapis.com/css2?family=Homemade+Apple&family=Satisfy&display=swap');

#homeImgWrapper{
    width: 100%; /* or whatever is required */
    max-height: 30em;
    text-align: center; /* ensures the image is always in the h-middle */
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}


#homeImg{
    position: relative;
    width: 100%; /* allows repositioning */
    left: 100%;/* move the whole width of the image to the right */
    margin-left: -200%;
    filter: brightness(80%);
    z-index: -5;
}

#homeHeader{
    position: absolute;
    z-index: 1;
    left: 0; 
    right: 0;
    margin: 0 auto;
    font-size: 8vw;
    color: white;
    font-family: 'Satisfy', cursive;
}

#homeImgWrapper2{
    display: flex;
    width: 100%;
    align-items: flex-start;
    margin: 0;
}

#homeMenuImg{
    width: 50%;
}

.homeSubWrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.5em;
    width: 100%;
    align-self: stretch;
    text-align: center;
}

.homeButton{
    margin-top: 1.5em;
    padding: 1em 2.5em;
    background-color: transparent;
    border: 5px solid #ffcad0;
    border-radius: 10px;
    font-size: 1em;
    cursor: pointer;
}

#homeImgWrapper3{
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    max-height: 800px;
}

#homeImg2{
    position: relative;
    object-fit: cover;
    width: 100%;
    height: 100%;
    filter: brightness(65%);
    z-index: -5;
}

#homeImgWrapper3 div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    z-index: 1;
    left: 0; 
    right: 0;
    margin: 0 auto;
    font-size: 2vw;
    color: white;
}

#homeImgWrapper3 h2 {
    font-size: 8vw;
    margin: 0;
    color: white;
}

#homeImgWrapper3 .homeButton, #homeImgWrapper5 .homeButton{
    color: white;
    font-weight: 600;
}

#homeImgWrapper4{
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
}

#homeCtrImg{
    width:50%;
}

.homeSubWrapper2{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1.5em;
    width: 100%;
    align-self: stretch;
}

#homeImgWrapper5{
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    max-height: 800px;
}

#homeImgWrapper5 h2 {
    font-size: 6vw;
    margin: 0;
}

#homeSubWrapper3{
    display: flex;
    flex-direction:column;
    justify-content:space-evenly;
    align-items:center;
    position: absolute;
    z-index: 1;
    left: 0; 
    right: 0;
    margin: 0 auto;
    font-size: 2vw;
    color: white;
}

#homeIconWrapper{
    display: flex;
    margin: 0 auto;
    justify-content: center;
    margin: 2em 0 0 0;
}

.homeIconDiv{
    margin: 0 3em;
    text-align: center;
}

.homeIconDiv p{
    font-size: 1em;
}

.homeIcons{
    color: #ffcad0;
    font-size: 5vw;
    margin-bottom: .2em;
}

#homeImgWrapper6{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 4em;
}

#homeImgWrapper6 h2{
    margin: 1.2em 0 0 0;
    font-size: 3vw;
}

#homeImgWrapper6 a h2{
    background-color: #ffcad0;
    padding: .5em;
    border-radius: 1em;
}


 
@media(max-width: 700px){

    #homeHeader{
        font-size: 8vw;
    }

    .homeButton{
        border-width: 2px;
    }

    #homeMenuImg{
        width: 60%;
    }

    .homeSubWrapper{
        margin: auto auto;
        width:95%;
        text-align: center;
        font-size: 2.5vw;
    }


    #homeCtrImg{
        width:60%;
    }
    
    .homeSubWrapper2{
        margin: auto auto;
        text-align: center;
        font-size: 2.5vw;
    }


    #homeIconWrapper{
        margin: 3em 0;
    }
    
    .homeIconDiv{
        margin: 0 2vw;
        text-align: center;
        font-size: 2vw;
    }
    

    #homeImgWrapper5 button{
        margin-top: 0;
    }

    #homeImgWrapper6 h2{
        margin: 1.2em 0 0 0;
        font-size: 6vw;
    }

}