@import url('https://fonts.googleapis.com/css?family=Arimo');

#coHeader, #coDesc{
    text-align: center;
    margin: 0;
    padding: .5em 0 0 0;
}

#coDesc{
    font-size: 1.3em;
    margin: 0 auto 1em auto;
    width: 75vw;
}

#ctrHeader{
    margin: .5em
}

#coError{
    color: red;
    margin: .3rem 0 0 0;
    font-size: .9em;
}

#coLogoImg{
    position: absolute;
    width: 5em;
    right: 1em;
    top: 1em;
    border-radius: 50%;
    padding-left: .2em;
    background-color: #FBCAD0;
}

#coReturn{
    position: absolute;
    left: 1em;
    top: 1em;
    font-weight: 600;
    background-color: #FBCAD0;
    padding: .2em .4em .2em 0;
    display: flex;
    align-items: center;
    border-radius: 5px;
    cursor: pointer;
    -webkit-box-shadow: 7px 8px 18px 0px rgba(0, 0, 0, 0.18);
    -moz-box-shadow: 7px 8px 18px 0px rgba(0, 0, 0, 0.18);
    box-shadow: 7px 8px 18px 0px rgba(0, 0, 0, 0.18);
}

#coReturnIcon{
    font-size: 1.5em;
}

#coBErrors{
    color: white;
    background-color: #E57373;
    border: solid 4px red;
    border-radius: 2em;
    width: 60vw;
    margin: 0 auto;
    padding: 1em
}

#coBErrors div{
    margin: .5em 0;
}

#coWrapper{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-end;
    flex-wrap: wrap-reverse;
}

.coInfoWrapper {
    margin: .5em;
    display: flex;
    justify-content: center;
    font-family: "Arimo";
    background-color: #FBCAD0;
    -webkit-box-shadow: 9px 13px 25px 0px rgba(0, 0, 0, 0.18);
    -moz-box-shadow: 9px 13px 25px 0px rgba(0, 0, 0, 0.18);
    box-shadow: 9px 13px 25px 0px rgba(0, 0, 0, 0.18);
    animation: slideUp 2000ms ease;
    border-radius: 25px;
}

#coDeliveryInfo{
    width: 40em;
}

#coCartInfo{
    width: 22.5em;
    height: 30em;
    position: relative;
}


@keyframes slideUp {
    0% {
        -webkit-transform: translateY(100%);
        transform: translateY(100%);
        visibility: visible;
    }

    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);

    }
}


#coInfoContainer {
    width: 35em;
    padding: 5% 5%;
}

#coForm {
    width: 100%;
}

.coTextboxDiv >*,
.coTextboxDiv2,
#coAddressDiv >*{
    margin-top: 1.2rem;
}

#coForm input{
    width: 100%;
    min-height: 25px;
    border: 0;
    font-size: .9rem;
    letter-spacing: .15rem;
    padding-left: .5em;
    border-radius: 4px;
}

.coTextboxDiv label,
.coTextboxDiv2 label,
#coAddress label,
#coAddressDiv label,
#coPayment label{
    display:inline-block;
    margin-bottom: .5rem;
}

#coForm label {
    text-transform: uppercase;
    font-size: .8rem;
    letter-spacing: 2px;
    color: #493b76;
}

.coHeader2, .coHeader3 {
    font-size: 1.5rem;
    margin: 0;
    color: #493b76;
    letter-spacing: 1px;
}

.coHeader3 {
    margin-top: 1em;
}

#coCartInfo .coHeader3{
    margin-top: .5em;
}

.coTextboxDiv {
    justify-content: space-between;
    display: flex;
    width: 100%;
}

.coTextboxDiv div{
    display: flex;
    flex-direction: column;
    width: 45%;
}

#coAddress{
    width: 60%;
}

#coApt{
    width: 30%;
}

#coAddressDiv {
    display: flex;
    justify-content: space-between;
}

#coAddressDiv div{
    width: 30%;
}

#coPayment{
    position: relative;
    width: 100%;
    margin-top: 1.2rem;
}

#coPayment div{
    background-color: white;
    padding: .1em;
    border-radius: 4px;
}

#coPayment img{
    margin: .5em 0 0 0;
    width: 20%;
}

#coButton {
    display: block;
    margin: 1.5em auto 0 auto;
    padding: .8em 1.3em;
    color: white;
    background-color: #4a3b76;
    text-transform: uppercase;
    border: 0;
    border-radius: 1.1em;
    letter-spacing: 2px;
    cursor: pointer;
}

#coButton:hover {
    animation-name: btn-hov;
    animation-duration: 550ms;
    animation-fill-mode: forwards;
}

@keyframes btn-hov {
    100% {
        background-color: #f1e8ff;
        color: #4a3b76;
        transform: scale(1.05)
    }
}

#coIconSubmitLoad{
    -webkit-animation:spin 2s linear infinite;
    -moz-animation:spin 2s linear infinite;
    animation:spin 2s linear infinite;
}

@-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

input:focus,
button:focus {
    outline: none;
}

#coCartOrdersWrapper{
    overflow-y: auto;
    position: absolute;
    bottom: 6rem;
    top: 3rem;
    width: 100%;
}

#coCartOrders{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

#coCartOrdersWrapper::-webkit-scrollbar {
    width: 10px;               /* width of the entire scrollbar */
}

#coCartOrdersWrapper::-webkit-scrollbar-track {
    background: #FBCAD0;        /* color of the tracking area */
}

#coCartOrdersWrapper::-webkit-scrollbar-thumb {
    background-color: #C7C7C7;    /* color of the scroll thumb */
    border-radius: 20px;       /* roundness of the scroll thumb */
}

.coCartItem{
    display: flex;
    justify-content: space-between;
    width: 85%;
    margin: .5em 0;
    padding: .5em;
    border: 3px solid #4a3b76;
    border-radius: 1.5em;
    background-color: #f1e8ff;
}
  
.coCartItemInfo{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 50%;
  }
  
.coCartItemName{
    margin: 0 0 .3rem 5%;
    font-size: 1.1em;
  }
  
.coCartItemImage{
    width: 45%;
    border-radius: 1.2em;
    height: 6rem;
    object-fit: cover;
}

.coCartItemCtrl{
    padding: 0 5%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
  
.coCartItemQty, .coCartItemPrice{
    font-size: 1.2em;
    -webkit-touch-callout: none; /* iOS Safari */
      -webkit-user-select: none; /* Safari */
       -khtml-user-select: none; /* Konqueror HTML */
         -moz-user-select: none; /* Old versions of Firefox */
          -ms-user-select: none; /* Internet Explorer/Edge */
              user-select: none;
}
  
.coCartItemIcon{
    font-size: 1.3em;
    color: #4a3b76;
    cursor: pointer;
}


#coTotalDiv{
    width: 85%;
    position: absolute;
    bottom: 0;
    height: 6rem;
}

.coCost{
    width: 100%;
    margin: .3rem 0;
    display: flex;
    justify-content: space-between;
}

#coTotal{
    font-size: 1.5em;
}

#coBottomSpace{
    margin-bottom: .5em;
}

@media (max-width: 1046px) {
    #coLogoImg{
        width: 3em;
        right: 1em;
        top: .5em;
        padding-left: .1em;
    }

    #coCartInfo {
        width: 40em;
    }

    #coCartOrders{
        width: 100;
    }

    .coCartItem{
        width: 85%;
    }

    .coCartItemInfo{
        width: 40%;
    }

    .coCartItemImage{
        width: 45%;
        border-radius: 1.2em;
        height: 7.5rem;
        object-fit: cover;
    }
}

@media (max-width: 750px) {
    #coReturn{
        font-size: .8em;
        top: 1.5em;
    }

    #coDeliveryInfo, 
    #coCartInfo {
        width: 95%;
    }
    #coDeliveryInfo{
        height: auto;
    }

    #coInfoContainer {
        width: 100%;
    }

    #coButton {
        width: 50%;
    }

    .coHeader2 {
        text-align: center;
    }

    .coTextboxDiv,
    #coAddressDiv{
        flex-direction: column;
        width: 100%;
        justify-content: space-between;
    }

    .coTextboxDiv div,
    #coAddressDiv div,
    #coAddress,
    #coApt,
    #coPayment label{
        align-items: center;
        flex-direction: column;
        width: 100%;
        display: flex;
    }

    .coTextboxDiv2{
        text-align: center;
    }

    #coForm input {
        margin: 5px 0;
        min-height: 30px;
    }
}