@import url('https://fonts.googleapis.com/css2?family=Homemade+Apple&family=Satisfy&display=swap');

#ctrHeader, .ctrDesc{
    text-align: center;
}

.ctrDesc{
    margin: 0 auto 1.5em auto; 
}

#ctrHeader{
    font-family: 'Satisfy', cursive;
    font-size: 2.5em;
    margin: .5em
}

#ctrBErrors{
    color: white;
    background-color: #E57373;
    border: solid 4px red;
    border-radius: 2em;
    width: 60vw;
    margin: 0 auto;
    padding: 1em
}

#ctrBErrors div{
    margin: .5em 0;
}

.ctrFormWrapper{
    margin: 0 auto;
    max-width: 50rem;
    border-radius: .7em;
    display: flex;
    justify-content: center;
    font-family: "Arimo";
    -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.1);
    -moz-box-shadow: 0 0 20px rgba(0,0,0,0.1);
    box-shadow: 0 0 20px rgba(0,0,0,0.1);
}

.ctrFormContainer{
    width: 90%;
    padding: 5% 5%;
}

#ctrForm{
    width: 100%;
}

#ctrForm >*{
    margin-top: 1em;
}

.ctrTextbox, .ctrTextbox2, #ctrForm select, #ctrForm textarea{
    border: none;
    background-color: hsl(0 0% 93%);
    border-radius: .25rem;
    padding: .75rem 1rem;
}

#ctrForm textarea{
    height: 15em;
}

#ctrForm label{
    text-transform: uppercase;
    font-size: .8rem;
    letter-spacing: 2px;
    margin-bottom: .5em;
}

.ctrDiv{
    justify-content: space-between;
    display: flex;
    width: 100%;
}

.ctrDiv2{
    display: flex;
    flex-direction: column;
    width: 45%;
}

.ctrDiv3{
    display: flex;
    flex-direction: column;
    width: 30%;
}

.ctrDivST{
    display: flex;
    flex-direction: column;
    width: 100%;
}

.ctrDiv2 span, .ctrDiv3 span, .ctrDivST span, .ctrTextAreaDiv span{
    color: red;
    margin: .3rem 0 0 .5rem;
    font-size: .9em;
}

.ctrTextAreaDiv{
    display: flex;
    flex-direction: column;
    width: 100%;
}

.ctrTextbox:focus,
.ctrTextbox2:focus, 
#ctrAddress:focus,
.ctrSelect:focus,
.ctrTextArea:focus{
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

#ctrDate{
    background-color: hsl(0 0% 93%);
}

#ctrButton{
    display: block;
    margin: 2em auto 0 auto;
    padding: .75rem 1rem;
    color: white;
    background-color: #4a3b76;
    text-transform: uppercase;
    border: 0;
    border-radius: .25rem;
    letter-spacing: 2px;
    cursor: pointer;
}

#ctrButton:hover {
    animation-name: btn-hov;
    animation-duration: 550ms;
    animation-fill-mode: forwards;
}

@keyframes btn-hov {
    100% {
        background-color: #f1e8ff;
        color: #4a3b76;
        transform: scale(1.05)
    }
}

#ctrbottomSpace{
    margin-bottom: 5em;
}

@media(max-width: 750px){
    .ctrFormWrapper{
        width: 90%;
    }

    .ctrFormContainer {
        width: 100%;
    }

    #ctrForm >* {
        margin-top: 0;
    }

    #ctrForm label{
        margin-top: 1em;
    }

    #ctrButton {
        width: 50%;
        margin-top: 2em;
    }

    .ctrDiv{
        flex-direction: column;
        width: 100%;
        justify-content: space-between;
    }

    .ctrDiv2, .ctrDiv3, .ctrDivST, .ctrTextAreaDiv{
        align-items: center;
        flex-direction: column;
        width: 100%;
        display: flex;
    }

    .ctrDiv2 input, .ctrDiv3 input, 
    .ctrDiv2 select, .ctrDivST input, .ctrTextArea{
        width: 100%;
    }
}