@import url('https://fonts.googleapis.com/css2?family=Homemade+Apple&family=Satisfy&display=swap');

#faqHeader{
    text-align: center;
    margin: .5em 0 0 0;
    font-family: 'Satisfy', cursive;
    font-size: 2.5em;
}

.faqCat{
    margin: 1em 0 .2em 0;
}

.faqDiv{
    margin: 0 auto 0 auto;
    width:85%;
}

.faqDiv2{
    margin: .4em 0;
    background-color:#f1e8ff;
    border-radius: 5px;
}

.faqQues, .faqAns{
    text-align: center;
}

.faqQues{
    margin: 0 1rem;
    font-size: 1.7em;
    padding: .5em 0 .25em 0;
    cursor: pointer;
}

.faqQues h3{
    margin: 0;
}

.faqIcon{
    font-size: 1.2em;
    margin: 0;
}

.faqAns{
    margin: 0 2rem;
    padding-bottom: 1em;
    font-size: 1.2em;
}

.faqDiv2:hover{
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}