@import url('https://fonts.googleapis.com/css2?family=Homemade+Apple&family=Satisfy&display=swap');

/*Recipes Page*/
#rcpHeader{
    text-align: center;
    font-family: 'Satisfy', cursive;
    font-size: 2.5em;
}

#rcpWarning{
    margin-top: 2em;
    text-align: center;
    padding: 0 1em;
}

#rcpWrapper{
    max-width: 90vw;
    margin: 0 auto 2.5em auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.rcpItem{
    margin: 1em;
    width: 20em;
    padding-bottom: .8em;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    border: solid;
    border-color: #f1e8ff;
    border-width: 1px;
    border-radius: 20px;
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
    overflow: hidden;
    word-wrap: break-word;
}

.rcpItem img{
    width: 100%;
    height: 15rem;
    object-fit: cover;
}

.rcpItem span{
    text-align: center;
    padding: 0 .5em;
    width: 100%;
    margin: .8rem 0 0 0;
    font-size: 1.3em;
    font-weight: 200;
}

#rcpbottomSpace{
    margin-bottom: 5em;
}

/*Recipe Article*/
#rcpaWrapper{
    padding: 2em;
    height: 100%;
    display: block;
}

#rcpaReturnIcon{
    position: absolute;
    font-size: 2.5em;
    color: #ffcad0;
    cursor: pointer;
    margin: .5em .5em;
}

#rcpaHeader{
    text-align: center;
    margin: 0;
}

.rcpaDiv h2{
    margin: 0 0 1em 0;
}

#rcpaImageDiv{
    max-width: 50rem;
    margin: 2em auto;
}

#rcpaImage{
    width: 100%;
    height: 20rem;
    object-fit: cover;
}

#rcpaDescDiv{
    max-width: 50rem;
    margin: 2em auto;
    font-size: 1.1em;
}

#rcpaInfoDiv{
    max-width: 50rem;
    margin: 2em auto;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    border: solid 3px #4a3b76;
    border-radius: 10px;
    padding: 1em 0;
    line-height: 1.5em;
}

#rcpaInfoDiv span{
    font-size: 1.2rem;
    font-weight: 600;
}

#rcpaIngDiv{
    max-width: 50rem;
    margin: 2em auto;
}

#rcpaIngDiv div{
    margin: 1em 0;
    display: flex;
    align-items: center;
}

#rcpaIngIcon{
    color: #ffcad0;
    margin: 0 1em;
}

#rcpaDirDiv{
    max-width: 50rem;
    margin: 2em auto 3em auto;
}

#rcpaDirDiv div{
    margin: 2em 0;
    display: flex;
    justify-content: space-between;
}

#rcpaDirNums{
    background-color: #ffcad0;
    color: white;
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#rcpaDirText{
    width: 85%;
    align-self: center;
}

#rcpaBTDiv{
    max-width: 50rem;
    margin: 2em auto;
    flex-wrap: wrap;
    border: solid 3px #4a3b76;
    border-radius: 10px;
    padding: 0 1em 1em 1em;
}
