@import url('https://fonts.googleapis.com/css2?family=Homemade+Apple&family=Satisfy&display=swap');

#cuHeader{
    text-align: center;
    margin: .5em 0;
    font-family: 'Satisfy', cursive;
    font-size: 2.5em;
}

#cuBErrors{
    color: white;
    background-color: #E57373;
    border: solid 4px red;
    border-radius: 2em;
    width: 60vw;
    margin: 0 auto;
    padding: 1em
}

#cuBErrors div{
    margin: .5em 0;
}

#cuWrapper, #cuIconWrapper{
    display: flex;
    margin: 0 auto;
    justify-content: center;
    padding: 0 2em;
}

#cuDescDiv{
    margin: 4em 5% 0 0;
    width:22%;
    font-size: .75em;
    text-align: center;
    word-wrap: break-word;
}

#cuDescIcon{
    margin: 0 auto;
    color: #4a3b76;
    font-size: 10rem;
    margin-top: .1em;
    -webkit-font-smoothing: subpixel-antialiased;
}

.cuFormWrapper{
    flex: 1;
    max-width: 50rem;
    border-radius: .7em;
    display: flex;
    justify-content: center;
    font-family: "Arimo";
    -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.1);
    -moz-box-shadow: 0 0 20px rgba(0,0,0,0.1);
    box-shadow: 0 0 20px rgba(0,0,0,0.1);

}

.cuFormContainer{
    width: 90%;
    padding: 5% 5%;
}

#cuForm{
    width: 100%;
}

#cuForm >*{
    margin-top: 1em;
}

.cuTextbox, .cuTextbox2, #cuForm textarea{
    border: none;
    background-color: hsl(0 0% 93%);
    border-radius: .25rem;
    padding: .75rem 1rem;
    margin-top: .3em;
}

#cuForm textarea{
    height: 15em;
}

#cuForm label{
    text-transform: uppercase;
    font-size: .8rem;
    letter-spacing: 2px;
}

.cuDiv{
    justify-content: space-between;
    display: flex;
    width: 100%;
}

.cuDiv2{
    display: flex;
    flex-direction: column;
    width: 45%;
}

.cuDivST{
    display: flex;
    flex-direction: column;
    width: 100%;
}

.cuDiv2 span, .cuDivST span{
    color: red;
    margin: .3rem 0 0 .5rem;
    font-size: .9em;
}

.cuTextAreaDiv{
    display: flex;
    flex-direction: column;
    width: 100%;
}

.cuTextArea:focus, .cuTextbox:focus{
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

#cuButton{
    display: block;
    margin: 2em auto 0 auto;
    padding: .75rem 1rem;
    color: white;
    background-color: #4a3b76;
    text-transform: uppercase;
    border: 0;
    border-radius: .25rem;
    letter-spacing: 2px;
    cursor: pointer;
}

#cuButton:hover {
    animation-name: btn-hov;
    animation-duration: 550ms;
    animation-fill-mode: forwards;
}

@keyframes btn-hov {
    100% {
        background-color: #f1e8ff;
        color: #4a3b76;
        transform: scale(1.05)
    }
}

#cuIconWrapper{
    margin: 3em 0 5em 0;
}

.cuIconDiv{
    margin: 0 3em;
    text-align: center;
}

.cuIconDiv p{
    font-size: 1.1em;
}

.cuIcons{
    color: #4a3b76;
    font-size: 5em;
    margin-bottom: .2em;
}


@media(max-width: 750px){
    #cuWrapper{
        width:auto;
    }

    #cuDescDiv{
        display: none;
    }

    .cuFormWrapper{
        width: 90%;
    }

    .cuFormContainer {
        width: 100%;
    }

    #cuForm >* {
        margin-top: 0;
    }

    #cuForm label{
        margin-top: 1em;
    }

    #cuButton {
        width: 50%;
        margin-top: 2em;
    }

    .cuDiv{
        flex-direction: column;
        width: 100%;
        justify-content: space-between;
    }

    .cuDiv2, .cuDivST, .cuTextAreaDiv{
        align-items: center;
        flex-direction: column;
        width: 100%;
        display: flex;
    }

    .cuDiv2 input, .cuDivST input, .cuTextArea{
        width: 100%;
    }

    #cuIconWrapper{
        flex-direction: column;
        margin-bottom: 2em;
    }

    .cuIconDiv{
        margin-bottom: 4em;
    }
}